
import{BrowserRouter, Route, Link} from 'react-router-dom'
import './App.css';
import './home.css';
import logo from "./ADL.png";
import { Component } from 'react';


class SignIn extends Component{

  constructor(props){
    super(props)
    this.state={
      passType:"password",
      passTypeBool:true,
      pass:'',

    }
  }

  showPass=(event)=>{
    if(this.state.passTypeBool){
      this.setState({
        passType:"text"
      })
    }
    else{
      this.setState({
        passType:"password"
      })
    }
    this.setState({
      passTypeBool:!this.state.passTypeBool
    })
  }

  request = async () => {
    
    var email = document.getElementById("Email");
    var pass = document.getElementById("Password");
    const url = "http://192.168.1.4:5000/logIn"; 
    var success={"message":"Success"};
    var body="{\"userId\":\""+email.value+"\",\"userPass\":\""+pass.value+"\"}";
    var json= await fetch(url, {
    body: body,
    headers: {
        "Content-Type": "application/json"
    },
    method: "POST"
    }) // https://cors-anywhere.herokuapp.com/https://example.com
    .then(response => response.text())
    json=JSON.parse(json);
    // alert(JSON.stringify(json));
    // alert("Login Success");
    if(JSON.stringify(json) === JSON.stringify(success)){
        window.location.replace("../deviceRemotePage/devicelist.html");
    }
    else{
        alert("Email and Password Don't Match");
        window.location.reload();
    }
}
  submitAPI = () => {
    this.request()
}
  render(){
    return(
      <div>
        <form  method="get" style={{display:"flex",flexDirection:"column"}} class="form ">
                        <input type="text" id="Email" placeholder="Email" autocomplete="on" required/>
                        <input type={this.state.passType} id="Password" placeholder="Password" autocomplete="on" required/>
                        <input id="submit-btn" onClick={this.submitAPI} type="button" name="submit" class="login" value="LOGIN"/>
                        <a href="forgotpass.html" style={{marginLeft:"190px",width:"9em",textAlign: "center",fontSize: "10px"}}>Forgot Password?</a>
                        <input type="checkbox" onClick={this.showPass} style={{marginLeft: "80px",marginTop: "-110px"}}/>
                        <label for="checkbox" style={{marginLeft: "98px",marginTop: "-18px",fontSize: "12px"}}>Show Password</label>               
        </form>
      </div>
    );
  }
}


class SignUp extends Component{
  constructor(props){
    super(props)
    this.state={
      passType:"password",
      passTypeBool:true,
      pass:'',
    }
  }

  showPass=(event)=>{
    if(this.state.passTypeBool){
      this.setState({
        passType:"text"
      })
    }
    else{
      this.setState({
        passType:"password"
      })
    }
    this.setState({
      passTypeBool:!this.state.passTypeBool
    })
  }

  request = async () => {
    alert("test");
    var email = document.getElementById("Email");
    var pass = document.getElementById("Password");
    var name = document.getElementById("Name");
    var company = document.getElementById("Company");
    var success={"message":"Success"}
    const url = "http://192.168.1.4:5000/signUp"; 
    var body="{\"userId\":\""+email.value+"\",\"userPass\":\""+pass.value+"\",\"userName\":\""+name.value+"\",\"userCompany\":\""+company.value+"\"}";
    var json=await fetch(url, {
    body: body,
    headers: {
        "Content-Type": "application/json"
    },
    method: "POST"
    }) // https://cors-anywhere.herokuapp.com/https://example.com
    .then(response => response.text());
    var signUPJson=JSON.parse(json);
    if(JSON.stringify(signUPJson) === JSON.stringify(success)){
        window.location.replace("signin.html");
    }
    else{
        alert("User is Already registered");
        window.location.reload();
    }
}

  submitAPI = () => {
    this.request()
}

  render(){
    return(
      <div>
        <form  method="get" style={{display:"flex",flexDirection:"column"}} class="form/">
                        <input style={{marginTop:"30px"}} type="text" id="Name" placeholder="Your Name"/>
                        <input style={{marginTop:"30px"}} type={this.state.passType} id="Password" placeholder="Your Password"/>
                        <input style={{marginTop:"30px"}} type="text" id="Email" placeholder="Your Email"/>
                        <input style={{marginTop:"30px"}} type="text" id="Company" placeholder="Company Name"/>
                        <input style={{marginTop:"30px"}} type="button"  onClick={this.submitAPI} class="login" value="SUBMIT"/>
                        <input type="checkbox" onClick={this.showPass} style={{marginLeft: "80px",marginTop: "-90px"}}/>
                        <label for="checkbox" style={{marginLeft: "98px",marginTop: "-18px",fontSize: "12px"}}>Show Password</label>
        </form>
      </div>
    );
  }
}


function None(){
  const logoStyle={
    width: "300px",
    marginLeft: "75px",
    marginTop: "10px",
    backgroundColor:"rgb(51, 51, 51)",
    borderRadius: "10px"
  }
  return(
    <div>
      <header className="App-header" >Powered By:</header>
      <img src={logo} style={logoStyle}></img>
    </div>

  );
}


function App() {
  return (
    <BrowserRouter>
      <div class="nav">
        
        <li class="vlsign"><Link to="/signUp">SIGN UP</Link></li>
        <li><Link to="/signIn">SIGN IN</Link></li>
      </div>
      
      <div> 
         <Route path="/" exact component={None}/>
         <Route path="/signUp" exact component={SignUp}/>
         <Route path="/signIn" exact component={SignIn}/>
      </div>
           
    </BrowserRouter>
  );
}

export default App;
